import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Flow from './App';
import Equus from "./pages/equus.js"
import Route from "./route.js"
import Hyrachotherium from './pages/hyrachotherium';
import Merychippus from './pages/merychippus';
import Mesohippus from './pages/mesohippus';
import Miohippus from './pages/miohippus';
import Pliohippus from './pages/pliohippus';

ReactDOM.render(
  <React.StrictMode>
    <Route path="/">
      <Flow />
    </Route>
    <Route path="/equus">
      <Equus />
    </Route>
    <Route path="/hyrachotherium">
      <Hyrachotherium />
    </Route>
    <Route path="/merychippus">
      <Merychippus />
    </Route>
    <Route path="/mesohippus">
      <Mesohippus />
    </Route>
    <Route path="/miohippus">
      <Miohippus />
    </Route>
    <Route path="/pliohippus">
      <Pliohippus />
    </Route>
  </React.StrictMode>,
  document.getElementById('root')
);
